@charset "UTF-8";
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /**
  * Reset liste à puce
  */
  .fr-accordions-group > li {
    list-style: none;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-accordion {
    box-shadow: inset 0 1px 0 0 #e5e5e5, 0 1px 0 0 #e5e5e5;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-accordion__btn {
    color: #161616;
  }
}
